import Vue from "vue";
import VueRouter from "vue-router";

// 引入组件
import index from "./pages/index.vue";
import HelloWord from "./pages/HelloWorld.vue";
import layout from "./pages/layout.vue";
import detail from "./pages/detail.vue";
import search from "./pages/search.vue";

// 要告诉 vue 使用 vueRouter
Vue.use(VueRouter);

const routes = [
    {
        path:"/",
        component: index,
        meta: { footShow: true }
    },
    {
        path: "/pages/HelloWord",
        component: HelloWord,
        meta: { footShow: true }
    },
    {
        path: "/pages/layout",
        component: layout,
        meta: { footShow: true }
    },
    {
        path: "/pages/detail",
        component: detail,
        name: "detail",
        meta: { footShow: false }
    },
    {
        path: `/pages/search`,
        component: search
    }
]

var router =  new VueRouter({
    routes
})
export default router;