<template>
  <div>
        <form action="/">
          <van-search
            v-model="value"
            show-action
            placeholder="请输入搜索关键词"
            @search="onSearch"
            @cancel="onCancel"
            @focus="onFocus"
          />
        </form>
        <div v-if="tag_show">
          <van-tag class="tag" size="medium" round @click="tagClick('jk')">jk</van-tag>
          <van-tag class="tag" size="medium" round type="primary" @click="tagClick('模特')">模特</van-tag>
          <van-tag class="tag" size="medium" round type="success" @click="tagClick('korean sex')">korean sex</van-tag>
          <van-tag class="tag" size="medium" round type="danger" @click="tagClick('萌白酱')">萌白酱</van-tag>
          <van-tag class="tag" size="medium" round type="warning" @click="tagClick('小鸟酱')">小鸟酱</van-tag>
        </div>
        <van-pull-refresh v-if="action" v-model="refreshing" @refresh="onRefresh">
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
          <van-card
            v-for="(item,index) in list"
            :key="index"
            num="2"
            price="2.00"
            desc="描述信息"
            :title="item.title"
            :thumb="item.img"
            @click="godeatil(item.url)"
          >
            <template #tags>
              <van-tag plain type="danger">标签</van-tag>
              <van-tag plain type="danger">标签</van-tag>
            </template>
            <template #footer>
              <van-button size="mini">按钮</van-button>
              <van-button size="mini">按钮</van-button>
            </template>
          </van-card>
            <!-- <van-cell v-for="(item,index) in list" :key="index" :title="item.title" /> -->
          </van-list>
        </van-pull-refresh>
  </div>
</template>

<script>
var _this = {}
export default {
  data() {
    return {
      tag_show:true,
      action:false,
      value: '',
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      page:0
    };
  },
  created(){

  },
  mounted() {
    _this=this;
  },
  methods: {
    onSearch() {
      if(this.list.length>0){
        this.page=0;
        this.list=[];
        this.onLoad();
      }else{
        this.action=true;
      }
    },
    onCancel() {
      this.$router.go(-1);
    },
    onFocus() {
    },
    tagClick(tag){
      this.tag_show=false;
      this.value=tag;
      this.action=true;
    },
    onLoad() {
      this.$axios.get('https://vod.now.sh/api/search?k='+this.value+'&p='+_this.page)
      .then(function (response) {
        if (_this.refreshing) {
          _this.list = [];
          _this.refreshing = false;
        }
        if (response.data.length > 0) {
          _this.list.push(...response.data);
        }else{
          _this.finished = true;
        }
        _this.loading = false;
        _this.page++;
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    onRefresh() {
      this.page=0;
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    godeatil(url){
      this.$router.push({ path: "/pages/detail", query: { url: url }});
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
}
.tag{
  margin: 10px;
}
</style>
