<template>
	<div>
		<van-nav-bar
			title="播放"
			left-arrow
			@click-left="onBack"
		/>
		<div class="myvideo">
			<video width="100%" height="100%" controls :src="play_url"></video>
		</div>
	</div>
</template>
<script>
var _this={}
export default {
  data() {
    return {
      play_url:""
    };
  },
  created(){
  },
  mounted() {
    _this=this;
    this.getPlayUrl();
  },
  methods: {
    getPlayUrl() {
      this.$axios.get('https://vod.now.sh/api/play?url='+this.$route.query.url)
      .then(function (response) {
        console.log(response.data);
        _this.play_url=response.data.high
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    onBack(){
		this.$router.go(-1);
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.myvideo{
    width: 100%;
    height: 200px;
}
</style>