<template>
  <div>
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item>1</van-swipe-item>
            <van-swipe-item>2</van-swipe-item>
            <van-swipe-item>3</van-swipe-item>
            <van-swipe-item>4</van-swipe-item>
          </van-swipe>
          <van-grid>
            <van-grid-item icon="photo-o" text="文字" />
            <van-grid-item icon="photo-o" text="文字" />
            <van-grid-item icon="photo-o" text="文字" />
            <van-grid-item icon="photo-o" text="文字" />
          </van-grid>
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
          <van-card
            v-for="(item,index) in list"
            :key="index"
            :tag="item.hd"
            currency="TIME:  "
            :price="item.time"
            :title="item.title"
            :thumb="item.img"
            @click="godeatil(item.url)"
          />
            <!-- <van-cell v-for="(item,index) in list" :key="index" :title="item.title" /> -->
          </van-list>
        </van-pull-refresh>
  </div>
</template>

<script>
var _this = {}
export default {
  data() {
    return {
      tab:[
        "直播",
        "推荐",
        "分类"
      ],
      active:1,
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      page:0
    };
  },
  created(){

  },
  mounted() {
    _this=this;
  },
  methods: {
    onLoad() {
      this.$axios.get('https://vod.now.sh/api/search?k=%E5%A8%87%E5%96%98&p='+_this.page)
      .then(function (response) {
        console.log(response.data);
        if (_this.refreshing) {
          _this.list = [];
          _this.refreshing = false;
        }
        if (response.data.length > 0) {
          _this.list.push(...response.data);
        }else{
          _this.finished = true;
        }
        _this.loading = false;
        _this.page++;
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    onRefresh() {
      this.page=0;
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    godeatil(url){
      this.$router.push({ path: "/pages/detail", query: { url: url }});
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
  }
</style>
